.container {
    max-width: 960px;
    padding: 0 2rem;
}
.header {
    background: #121212;
    height: 90px;
}
.header-con {
    display: flex;
    justify-content: space-between;
    height: 80px;
    align-items: baseline;
}
.logo-container a {
    color: #fff;
    text-decoration: none;
    font-weight: bold;
    font-size: 2rem;
}
.logo-container img {
    max-width: 85px;
}
.logo-container b{
    color: #FFCF01;
    font-family: Sedan;
    font-style: normal;
    font-weight: normal;
}
.menu {
    display: flex;
    list-style-type: none;
}
.menu li {
    padding-right: 2rem;
}
.menu li a {
    text-decoration: none;
    color: #FFCF01;
    font-family: Sedan;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;

    border: 2px solid transparent;
    background: #121212;
    padding: .5rem 1rem;
    transition: 0.3s;
    margin-top: 1rem;
    border-radius: 5px;
    text-transform: uppercase;
}
.menu li a:hover {
    border: 2px solid #fff;
    background: transparent;
    color: #fff;
}
.mobile-menu {
    display: none;
}
.liner {
    max-width: 100%;
    height: 0px;
    border: 3px solid #D4BC8C;
}

@media screen and (max-width: 480px) {
    .mobile-menu {
        display: block;
        color: #fff;
        font-size: 2rem;
    }
    .header {
        padding: 0 2rem;
    }
    .menu {
        width: 100%;
        height: auto;
        display: flex;
        position: absolute;
        flex-direction: column;
        top: 64px;
        left: -100%;
        opacity: 0;
        transition: 0.5s ease;
       
    }
    .menu.active {
        background: #111;
        left: 0;
        opacity: 1;
        z-index: 1;
        padding-left: 0;
        align-content: center;
        transition: 0.5s ease;
    }
    .menu li {
        padding-right: 0 ;
    }
    .menu-link {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 10vw;
        padding: 10px 0;
    }
    .logo-container a {
        color: #fff;
        text-decoration: none;
        font-weight: bold;
        font-size: 20px;
    }
    .logo-container img {
        max-width: 45px;
    }
    .logo-container {
        padding: 2rem 0;
    }
}

@media screen and (max-width: 768px) {
    .logo-container a {
        color: #fff;
        text-decoration: none;
        font-weight: bold;
        font-family: Sedan;
        font-style: normal;
        font-size: 20px;
    }
    .logo-container img {
        max-width: 30px;
    }
    .logo-container {
        padding: 2rem 0;
    }
}